import {
  BaseActionParams,
  CustomActionModalProps,
  ResolvedAction,
} from '../../exports/types';
import {
  BulkDeleteActionConfig,
  CustomActionConfig,
  DividerActionConfig,
} from './base';

export type BulkActionItemConfig = BulkDeleteActionConfig | CustomActionConfig;

type BulkActionItem = {
  type: 'action';
  action: {
    item: BulkActionItemConfig;
  };
  menu?: never;
};

type BulkActionMenu = {
  type: 'menu';
  menu: {
    label: string;
    items: (BulkActionItemConfig | DividerActionConfig)[];
  };
  action?: never;
};

export interface BulkActionsConfig {
  primaryActions?: (BulkActionItem | BulkActionMenu)[];
  secondaryActions?: (BulkActionItemConfig | DividerActionConfig)[];
}

export interface BulkActionsActionParams {
  selectedValues: any[];
  total: number;
}

export interface CustomBulkActionsActionParams extends BaseActionParams {
  actionParams: BulkActionsActionParams;
}
export type CustomBulkActionsActionResolver = (
  params: CustomBulkActionsActionParams,
) => ResolvedAction;

export interface CustomBulkActionsActionModalProps
  extends CustomActionModalProps {
  actionParams: BulkActionsActionParams;
}
